@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    
    font-family: 'Mulish', sans-serif !important;
}

:root {
    --color : rgb(126, 54, 194) !important;
}
.nav_class {
    color: rgb(126, 54, 194) !important;
    color: var(--color) !important;
}
.nav_class span {
    border-bottom: 2px solid rgb(126, 54, 194);
    border-bottom: 2px solid var(--color);
    padding: 3px;
}
.navbar-brand {
    width: 400px;
}
.nav-link {
    padding: 0px 20px !important;
    font-weight: bold;
    font-size: 1.5rem ;
    
}
.nav-link span:hover {
    background-color: rgb(126, 54, 194) !important;
    background-color: var(--color) !important;
    color: #fff !important;
   
} 
@media(max-width:768px){
    .navbar-brand {
        width: 300px;
    }
}
@media(max-width:420px){
    .navbar-brand {
        width: 200px;
    }
}

.hero_description1 {
    letter-spacing: 3px;
    font-size: 1.2rem;
    font-family: 'JetBrains Mono', monospace;
}

.hero_btn {
    text-decoration: none;
    font-size: 1.2rem;
    border: 1px solid rgb(3, 3, 145);
    padding: 10px;
    border-radius: 20px;
    transition: 0.3s;
}
.hero_btn:hover {
    transition: 0.3s;
    background-color:  rgb(96, 96, 218);
    color: #fff;
}
.hero_img_div {
    max-width: 700px;
    max-height: 600px;
  
}


@media(max-width : 768px){
    .btn_hero {
        margin-top: 25px !important;
    }
    .hero_description {
        margin-top: 50px;
    }
}
.ourMission {
    width: 100%;
    -webkit-clip-path: polygon(48% 4%, 100% 0, 99% 49%, 100% 100%, 48% 97%, 0 100%, 1% 51%, 0 0);
            clip-path: polygon(48% 4%, 100% 0, 99% 49%, 100% 100%, 48% 97%, 0 100%, 1% 51%, 0 0);
    color: white;
    background-color: rgb(88, 45, 129);
}
.ourMissionHedding {
    font-size: 2.5rem;
    font-weight: bold;
    text-align: center;
}
.ourMissionDiscription {
    font-size: 1.4rem;
}

@media(max-width : 768px){
    .ourMissionHedding {
        font-size: 2rem;
        font-weight: bold;
        text-align: center;
    }
   
    .ourMissionDiscription {
        font-size: 1rem;
    }
    
}

.about_hedline {
    border-bottom: 1px solid rgb(126, 54, 194);
}
.About h1 span {
    color: rgb(126, 54, 194);
    font-size: 2.5rem;
    font-weight: bold;
}
.about_discription h1{
    text-align: center;
    margin: 10px;
}
.about_discription p {
    font-size: 1.2rem;
    text-align: justify;
}
.about_discription a {
    border: 1px solid rgb(126, 54, 194);
    text-decoration: none;
    padding: 8px 20px;
    font-size: 1.5rem;
    border-radius: 10px;
  transition: 0.2s;
}
.about_discription a:hover {
    background-color:  rgb(126, 54, 194);
    transition: 0.3s;
    color: #fff;

}
@media(max-width:768px){
    .about_discription p {
        font-size: 1rem;
    }
    .about_discription a {
        font-size: 1rem;
    }
}
.card {
    height: 500px;
    transition: 0.3s;
}
.card:hover  {
    transition: 0.3s;
    background-color: rgb(126, 54, 194);
    color: white;
  }

  .card:hover  .btn-contact {
      background-color: #fff;
  }
.card_img {
    width: 100%;
}
.card_img img {
    border-radius: 50%;
    width: 200px;
}
.stage {
    color: rgb(126, 54, 194) ;
}

.card:hover .stage {
    transition: 0.3s;
    background-color: rgb(126, 54, 194);
    color: white;
  }

.Deliver_heading {
    color: rgb(126, 54, 194);
    margin-top : 150px
}

@media(max-width : 768px){
    .Deliver_heading {
        margin-top : 50px;
    }
    .Deliver img {
        margin-top: 50px;
    }
}
.service_title {
    color: rgb(126, 54, 194);
    font-weight: bold;
}
.service_title span {
    border-bottom: 2px solid rgb(126, 54, 194);
}
.Service .card {
    height: 250px;
}
.Service_icon {
    font-size: 100px !important;
    color: rgb(126, 54, 194);
}
.Service .card:hover .Service_icon {
    color: #ffff;
}
.Earn {
    width: 100%;
    background:  rgb(126, 54, 194) !important;
    color: white;
}
.Earn  a {
    text-decoration: none;
    padding: 8px 20px;
    font-size: 1.4rem;
    border: 2px solid #fff;
    color: #fff;
    border-radius: 20px;
    transition: 0.3s;
    margin-top: 20px !important;
    
}
.Earn  a:hover {
    background-color: #fff;
    color: rgb(126, 54, 194) ;
    transition: 0.3s;
}
.contact {
    background-image: url(/reactWebsiteV2.0/static/media/Contact.2735e2fd.png);
    background-repeat: no-repeat;
    background-position: center;
}
